.footerLink {
  text-decoration: none;
  color: #757575;
  font-weight: 800;
  font-size: 40px;
}

.footerLink:hover {
  text-decoration: underline;
}

.dark-table tr {
  color: #c1c3c2;
}

.dark-table tr > td:first-child {
  color: #ffffff;
  text-align: center;
}

.dark-table thead > tr {
  color: #fc6e55;
  font-size: 42px;
  font-weight: bold;
}

.alternatives-table tr {
  line-height: 1.5;
}

.alternatives-table tr > td:first-child {
  font-weight: bold;
  text-align: center;
}

.alternatives-table thead > tr {
  font-weight: bold;
}
